import s from './ProfileInfo.module.css';
import userPhoto from "../../../Assets/Images/user.png";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";

const ProfileInfo = (props) => {

    return <div>
        <div className={s.content}>
            <img src="https://wallbox.ru/resize/1280x1024/wallpapers/main2/201717/abstrakcia-tekstura-fon-kvadraty.jpg"
                 alt=""/>
        </div>
        <div className={s.description}>
            <img src={ props.profile.photos.small != null? props.profile.photos.small : userPhoto} className={s.avatar} alt=''/>
            {props.profile.fullName}
            <div>About me: {props.profile.aboutMe}</div>
            <ProfileStatusWithHooks status={props.status} updateStatus={props.updateStatus}/>
        </div>
    </div>
}

export default ProfileInfo;
